import React from 'react';

export default class WordImg extends React.Component{
    constructor(){
        super()
        this.state={
            newsData:[{name:1,root:2}],
            content:[
                {item:'内容一'},
                {item:'内容二'},
                {item:'内容三'}
            ],
            showShare:false,
            realShow:false
        }

    }
    componentDidMount(){
        var newdateArr = "badiu"

        var _this = this;
        $.ajax({
            url:"/api/user/signup",

            type:"get",
            async:'false'
        }).done(function(data){

            console.log( $.type(data))

            _this.setState({newsData:data})
        })
    }
    showImg(event){
        this.setState({showShare:true})
        if(this.state.showShare)
        {
            this.setState({realShow:true})
        }
        console.log(event)
        //"shareButton"
    }
    showImgReal(index){
        return this.state.realShow?"shareButton-active":"shareButton"
    }
    render(){



        return(
            <div className="newsImgparent">
                {this.state.newsData.map( (item,index) =>{
                    return(
                        <div key={index} className="newsImg">
                            <div>
                                 <img src={'public/css/img/'+item.img+".jpeg"}/>
                            </div>

                            <div className="fx1" >
                                <h2>{item.word}</h2>
                                <div onMouseEnter={this.showImg.bind(this)} className={this.showImgReal(index)}>
                                    <div className="sharBoard">

                                    </div>
                                </div>

                             </div>

                        </div>
                        )

                })}
            </div>
        )
    }
}


